
import { defineComponent } from "vue";
import jsonp from "jsonp";
import Icon from "@/components/shared/Icon.vue";
import SifButton from "@/components/shared/SifButton.vue";

const MAILCHIMP_URL = "https://finance.us2.list-manage.com/subscribe/post-json";

export default defineComponent({
  components: { Icon, SifButton },
  props: {},
  data() {
    return {
      active: false,
      email: "",
      submitted: false,
    };
  },
  setup() {
    return {};
  },
  methods: {
    toggleActive() {
      this.active = !this.active;
    },
    async submitEmail() {
      this.submitted = true;
      const query = `u=400787e0a5e23ec37b7b51f74&id=c1ee83387b&EMAIL=${encodeURIComponent(
        this.email,
      )}`;
      const url = `${MAILCHIMP_URL}?${query}`;
      jsonp(url, { param: "c" });
      // TODO - Set this as a callback to jsonp
      // , (error, data) => {
