
import { defineComponent } from "vue";
export default defineComponent({
  inheritAttrs: false,
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    absolute: {
      type: Boolean,
      default: false, // Skips vue routing
    },
    block: {
      type: Boolean,
      default: false,
    },
    connect: {
      type: Boolean,
      default: false,
    },
    active: {
      type: Boolean,
      default: false,
    },
    medium: {
      type: Boolean,
      default: false,
    },
    className: {
      type: String,
    },
    primary: {
      type: Boolean,
    },
    primaryOutline: {
      type: Boolean,
    },
    secondary: {
      type: Boolean,
    },
    ghost: {
      type: Boolean,
    },
    small: {
      type: Boolean,
    },
    success: {
      type: Boolean,
    },
    round: {
      type: Boolean,
    },
    nocase: {
      type: Boolean,
      default: false,
    },
    to: {
      type: String,
      default: "",
    },
  },

  data() {
    return {
      classes: {
        block: this.block,
        connect: this.connect,
        active: this.active,
        medium: this.medium,
        primary: this.primary,
        "primary-outline": this.primaryOutline,
        secondary: this.secondary,
        className: this.className,
        ghost: this.ghost,
        small: this.small,
        nocase: this.nocase,
        success: this.success,
        round: this.round,
      },
    };
  },
});
